<template>
    <div class="w-filter-order" :class="{ on: visible }">
        <span class="u-label" @click="toggleFilter">
            <span class="u-current-order"
                > : {{ current || "" }}</span
            >
            <span class="u-toggle">
                <i class="el-icon-arrow-down"></i>
                <i class="el-icon-arrow-up"></i>
            </span>
        </span>
        <span class="u-options">
            <span
                class="u-mode u-update"
                :class="{ on: order == 'update' }"
                @click="filter('update')"
                ><i class="el-icon-refresh"></i> </span
            >
            <span
                class="u-mode u-podate"
                :class="{ on: order == 'podate' }"
                @click="filter('podate')"
                ><i class="el-icon-sort"></i> </span
            >
            <slot></slot>
        </span>
    </div>
</template>

<script>
const order_map = {
    update: "",
    podate: "",
    favs: "",
    likes: "",
    downs: "",
};
export default {
    name: "orderBy",
    props: [],
    data: function() {
        return {
            visible : false,
            order: "update",
        };
    },
    computed: {
        current: function() {
            return order_map[this.order]
        },
    },
    methods: {
        toggleFilter: function() {
            this.visible = !this.visible;
        },
        filter: function(key) {
            this.order = key;
            this.$emit("filter", { type: 'order', val: key });
            this.visible = false
        },
    },
    mounted: function() {},
    components: {
    },
};
</script>

<style lang="less">
    @import '../../assets/css/order-by.less';
</style>